body {
  background-color: #252525;
}

.side-nav {
  height: 779px;
  width: 73px;
  background-color: #454543;
}

.hamburger {
  color: #e5e5e5;
  font-size: 40px !important;
  padding: 15px;
}

.links {
  padding-top: 50px;
  padding-left: 5px;
}

.vertical-text-links {
  -webkit-writing-mode: tb-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: tb-rl;
  font-size: 12px;
  color: #cccccc;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  padding-top: 30px;
  font-size: 20px;
  cursor: pointer;
  font-family: 'Do Hyeon', sans-serif;
}

.social-icons {
  display: inline-grid;
  padding: 10px;
  margin-top: 460px;
}

.social-icons svg{
  padding: 10px;
}

.intro {
  top: 200px;
  left: 400px;
  position: fixed;
  color: #cccccc;
}

.intro-username {
  border: white 1px solid;
  padding: 20px;
  font-size: 30px;
  font-weight: 700;
  max-width: 300px;
}

.intro-heading {
  font-size: 40px;
  font-weight: 900;
}

.intro-contact {
  margin-top: -30px;
  font-size: 20px;
  color: grey;
  font-weight: 900;
}

.intro-text {
  font-size: 20px;
  margin-top: 50px;
  color: grey;
  max-width: 800px;
  letter-spacing: 2px;
}

a{
  color:black;
}

/* For changing colors of icon on hover */
.linkedin_link:hover {
  color: #0077B5;
}
.insta_link:hover {
  color: #e95950;
}

.cv-link svg{
  color: darkgray;
}



